<template>
    <div class="ShiAddress">
        <Header>
            <i slot="first" class="iconfont icon-prev" @click="$router.go(-1)"></i>
            <p slot="mid">编辑地址</p>
            <div slot="last">
                <span @click="show=!show" v-show="!show">编辑</span>
                <span @click="show=!show" v-show="show">完成</span>
            </div>
        </Header>
        <ul>
            <li v-for="(v,i) in list" :key="i">
                <p>{{v.address}}</p>
                <p>{{v.phone}}</p>
                <span v-show="show" @click="mistake(v.id,i)">X</span>
            </li>
        </ul>

        <div class="NewAddress" @click="NewAddress()">
            <div>新增地址</div>
            <div><van-icon name="arrow"/></div>
        </div>
    </div>
</template>
<script>
import Header from "@/page/City/Header"
export default {
     components:{
        Header
    },
   data() {
      return {
          list:[],
          show:false
      }
   },
   created() {
        this.axios.get("http://elm.cangdu.org/v1/users/38150/addresses").then((res)=>{
            console.log(res)
                this.list=res.data
        })
   },
   methods: {
       NewAddress(){
           this.$router.push("/home/newAddress")
       },
       mistake(id,i){
        //    this.list.splice(i,1)
        this.axios.delete(`http://elm.cangdu.org/v1/users/38150/addresses/${id}`).then((res)=>{
            this.list.splice(i,1)
        })
       }
   },
   watch: {
       show(v){
           console.log(v)
       }
   },
}
</script>

<style lang="scss">
body{
    background: #F2F2F2;
    font-size: 0.25rem;
}
.ShiAddress{
    width: 100%;
    .NewAddress{
        width: 100%;
        margin-top:0.25rem;
        background: white;
        display: flex;
        height: 0.8rem;
        >div:nth-of-type(1){
            font-size: 0.35rem;
            margin: 0.18rem 0.25rem;
        }
        >div:nth-of-type(2){
            font-size: 0.35rem;
            margin-left: 4.8rem;
            margin-top: 0.2rem;
            color: gray;
        }
    }
    ul{
        width: 100%;
        margin-top:1rem;
        background: white;
        li{
            border-bottom: 1px solid rgb(204, 203, 203);
            line-height: 0.55rem;
            position: relative;
            p{
                margin-left: 0.25rem;
            }
            span{
                position: absolute;
                margin-top: -0.85rem;
                right: 0.15rem;
                color: gray;
            }
        }
        li:nth-of-type(1){
            background: #fff8c3;
        }
    }
}
</style>


